@import "../../data/styles.css";

/* Container for the logo */
.about-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 50px; /* increased spacing to match homepage */
}

/* Fixed logo styling */
.about-logo {
	display: flex;
	position: fixed;
	top: 4vh;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	z-index: 999;
}

/* Top-level container */
.about-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px; /* spacing from nav */
}

/* Main layout split */
.about-main {
	display: flex;
	justify-content: space-between;
}

/* Left content (image, socials) */
.about-left-side {
	display: flex;
	flex-direction: column;
	flex-basis: 300px;
}

/* Right content (text) */
.about-right-side {
	flex-basis: 900px;
}

/* Typography */
.about-title {
	width: 100% !important;
	font-size: 90%; /* Slightly reduced */
}

.about-subtitle {
	width: 80% !important;
	font-size: 85%; /* Slightly reduced */
	padding-top: 10px;
}

/* Image styling */
.about-image-container {
	width: 370px;
}

.about-image-wrapper {
	display: inline-block;
	overflow: hidden;
	width: 100%;
	border-radius: 10%;
	transform: rotate(3deg);
	transition: transform 0.4s ease;
	transform-origin: center center;
}

.about-image-wrapper img {
	display: block;
	width: 100%;
	object-fit: cover;
}

/* Social section */
.about-socials {
	padding-top: 50px;
	padding-bottom: 50px;
}

.about-socials-mobile {
	display: none;
}

/* Tablet and small laptop */
@media (max-width: 1024px) {
	.about-logo {
		z-index: 100;
	}

	.about-main {
		flex-direction: column;
	}

	.about-left-side,
	.about-right-side {
		flex-basis: 100%;
	}

	.about-left-side {
		order: 1;
	}

	.about-right-side {
		order: 2;
	}

	.about-subtitle {
		padding-top: 20px !important;
	}

	.about-image-container {
		width: 100%;
		padding-bottom: 40px;
	}

	.about-socials {
		display: none;
	}

	.about-socials-mobile {
		display: flex;
		padding-top: 30px;
	}
}

/* Mobile */
@media (max-width: 600px) {
	.about-container {
		padding-top: 120px;
	}

	.about-logo {
		display: none; /* Hide logo on small screens */
	}

	.about-image-container {
		width: 80%; /* Restrict image width */
		margin: 0 auto;
	}

	.about-title,
	.about-subtitle {
		font-size: 85%;
		width: 100% !important;
	}
}

.page-content {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.content-wrapper {
	flex: 1;
	display: flex;
	flex-direction: column;
}

.about-container {
	flex: 1;
}
