@import "../../data/styles.css";

.homepage-logo-container {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 130px;
}

.homepage-container {
	padding-top: 0px;
}

.homepage-first-area {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.homepage-first-area-left-side {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.homepage-title {
	width: 85% !important;
}

.homepage-subtitle {
	width: 85% !important;
}

.homepage-first-area-right-side {
	display: flex;
	align-items: center;
}

.homepage-image-container {
	width: 100%;
}

.homepage-image-wrapper {
	overflow: hidden;
	width: 370px;
	height: 370px;
	border-radius: 10%;
	/* initial tilt */
	transform: rotate(3deg);
	/* animate both scale & rotation */
	transition: transform 0.4s ease;
	transform-origin: center center;
}

.homepage-image-wrapper img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center;
	cursor: pointer;
}

/* on hover: zoom & slightly increase rotation of the entire wrapper */
.homepage-image-wrapper:hover {
	transform: rotate(1deg) scale(1.15);
}

.homepage-socials {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 30px;
}

.homepage-social-icon {
	padding-right: 20px;
	font-size: 25px;
	transition: color 0.2s ease-in-out;
	color: var(--secondary-color);
}

.homepage-social-icon:hover {
	color: var(--primary-color);
}

.homepage-after-title {
	display: flex;
}

.homepage-projects {
	flex-basis: 300px;
	padding-top: 40px;
}

.homepage-articles {
	flex-basis: 900px;
	padding-top: 30px;
	margin-left: -30px;
}

.homepage-article {
	padding-bottom: 5px;
}

.homepage-works {
	flex: auto;
	padding-top: 30px;
}

@media (max-width: 1024px) {
	.homepage-first-area {
		flex-direction: column;
	}

	.homepage-first-area-left-side {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 100%;
		order: 2;
	}

	.homepage-title {
		width: 100% !important;
		margin-top: 20px;
		order: 2;
	}

	.homepage-subtitle {
		width: 100% !important;
		margin-top: 10px;
		order: 3;
	}

	.homepage-first-area-right-side {
		width: 100%;
		order: 1;
	}

	.homepage-image-container {
		padding-top: 50px;
		padding-bottom: 50px;
		margin-left: 10px;
		order: 1;
	}

	.homepage-after-title {
		flex-direction: column;
	}

	.homepage-projects {
		flex-basis: auto;
	}

	.homepage-articles {
		flex-basis: auto;
	}

	.homepage-works {
		flex-basis: auto;
		margin-left: -25px;
		margin-right: -25px;
	}
}

@media (max-width: 600px) {
	.mobile-hidden {
		display: none !important;
	}

	.homepage-container {
		padding-top: 100px;
	}

	/* Add these new styles for mobile image sizing */
	.homepage-image-container {
		width: 100%;
		padding-top: 20px;
		padding-bottom: 20px;
		display: flex;
		justify-content: center;
	}

	.homepage-image-wrapper {
		width: 280px !important; /* Reduced from 370px */
		height: 280px !important; /* Reduced from 370px */
		max-width: 90%; /* Ensures it doesn't exceed screen width */
	}

	.homepage-first-area-right-side {
		justify-content: center;
	}

	/* Optional: Reduce hover effect on mobile */
	.homepage-image-wrapper:hover {
		transform: rotate(3deg) scale(1.05); /* More subtle effect */
	}
}
