.all-projects-container {
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	padding-top: 20px;
	padding-bottom: 20px;
}

.all-projects-project {
	flex: 1 1 calc(33.333% - 20px);
	min-width: 250px;
	background-color: var(--background-color);
	border-radius: 15px;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
	transition: transform 0.2s ease, box-shadow 0.2s ease;
	overflow: hidden;
	position: relative;
}

.all-projects-project:hover {
	transform: translateY(-5px);
	box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

/* Make project container a flex column to position link at bottom */
.all-projects-project > div {
	display: flex;
	flex-direction: column;
	height: 100%;
	padding: 0px;
	padding-top: 10px;
}

/* Position logo absolute in top right */
.all-projects-project .project-logo {
	position: absolute;
	top: 15px;
	right: 15px;
	width: 30px;
	height: 30px;
	z-index: 1;
}

/* Add ellipsis to description and increase space */
.all-projects-project .project-description {
	display: -webkit-box;
	-webkit-line-clamp: 4;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;
	margin-bottom: 15px;
	line-height: 1.5;
	min-height: 85px;
}

/* Ensure link stays at bottom */
.all-projects-project .project-link {
	margin-top: auto;
	padding-top: 10px;
}

@media (max-width: 992px) {
	.all-projects-project {
		flex: 1 1 calc(50% - 20px);
	}
}

@media (max-width: 600px) {
	.all-projects-container {
		flex-direction: column;
	}

	.all-projects-project {
		flex: 1 1 100%;
		margin-bottom: 5px;
	}
}
