/* Global container wrapper to ensure consistent layout across pages */
* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	width: 100%;
	overflow-y: scroll;
}

.page-wrapper {
	display: flex;
	flex-direction: column;
	width: 100%;

	margin: 0 auto;
}

.contact-container {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	margin-top: 50px; /* Spacing below navbar */
	/* padding: 0 20px; */
	height: 100%;
	width: 100%; /* Ensure it takes full width */
}

.contact-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 100px; /* increased for consistency */
}

/* Fixed logo style */
.contact-logo {
	display: flex;
	position: fixed;
	top: 4vh;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	z-index: 999;
}

/* Subtitle text */
.contact-subtitle {
	width: 100% !important;
	font-size: 90%; /* slight reduction */
	padding-top: 10px;
}

/* Socials wrapper */
.socials-container {
	display: flex;
	flex-direction: column;
	margin-top: 80px;
}

/* Social icons layout */
.contact-socials {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	gap: 20px;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
	.contact-container {
		margin-top: 100px;
	}

	.contact-subtitle {
		font-size: 85%;
	}
}

@media (max-width: 600px) {
	.contact-logo {
		display: none; /* Hide logo on mobile */
	}

	.contact-container {
		margin-top: 30px;
		padding: 0;
	}

	.contact-subtitle {
		font-size: 85%;
		text-align: left;
		width: 100%;
	}

	.contact-socials {
		flex-wrap: wrap;
	}
}
