@import "../../../data/styles.css";

/* Container for the entire navbar */
.nav-container {
	margin: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

/* Main navbar wrapper */
.navbar {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	top: 3vh;
	z-index: 999;
	width: 100%;
	pointer-events: none; /* prevent side overflow interactions */
}

/* Background panel of the navbar */
.nav-background {
	width: 450px;
	height: 50px;
	background: #fff;
	box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
	border-radius: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	pointer-events: auto; /* restore interaction for content */
}

/* Nav list (holds the items) */
.nav-list {
	display: flex;
	justify-content: space-between;
	align-items: center;
	list-style: none;
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0 30px;
}

/* Individual item */
.nav-item {
	font-weight: bold;
	font-size: 90%; /* Was 95% */
	display: flex;
	align-items: center;
	height: 100%;
}

/* Links inside items */
.nav-item a {
	text-decoration: none;
	color: var(--primary-color);
	padding: 0 10px;
}

/* Active item styling */
.nav-item.active a {
	color: var(--link-color) !important;
}

/* Hover behavior */
.nav-item a:hover {
	color: var(--link-color);
	transition: color 0.3s ease-in-out;
}

/* Mobile styles */
@media (max-width: 600px) {
	.navbar {
		justify-content: center;
	}

	.nav-background {
		width: 80%;
		max-width: 450px;
		height: 45px;
	}

	.nav-list {
		height: 45px;
		padding: 0 15px;
	}

	.nav-item {
		font-size: 85%;
	}
}
