@import "../../../data/styles.css";

/* Works Section - Base Styles */
.works-body {
	margin-bottom: 10px;
	padding-bottom: 50px;
	width: 100%;
	overflow-x: hidden;
}

.work {
	display: flex;
	width: 400px;
	position: relative;
	margin: 0; /* Left-aligned */
	padding: 10px 0;
	min-height: 70px; /* Ensure space for absolutely positioned elements */
}

.space {
	height: 60px;
}

.work-image {
	height: 30px;
	width: 30px;
	border-radius: 50%;
	outline: 6px solid white;
	box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.25);
	flex-shrink: 0;
}

.work-title {
	font-size: 15px;
	font-weight: 700;
	padding-left: 20px;
	margin-top: -3px;
	color: var(--secondary-color);
}

.work-subtitle {
	position: absolute;
	font-size: 14px;
	color: var(--secondary-color);
	padding-top: 22px;
	padding-left: 50px;
}

.work-position {
	position: absolute;
	font-size: 12px;
	color: var(--secondary-color);
	padding-top: 45px;
	padding-left: 50px;
}

.work-duration {
	position: absolute;
	width: 370px;
	font-size: 12px;
	padding-top: 20px;
	text-align: right;
	color: var(--tertiary-color);
	right: 0;
}

/* Mobile Responsive Styles */
@media (max-width: 600px) {
	.works-body {
		padding: 0 0px 30px;
	}

	.work {
		width: 100%;
		max-width: 100%;
		flex-direction: column; /* Stack vertically */
		padding: 0px;
		background: rgba(255, 255, 255, 0.05);
		border-radius: 10px;
		margin-bottom: 0px;
		min-height: auto; /* Reset height */
	}

	.work-image {
		margin-bottom: 15px;
	}

	.work-title {
		position: static;
		margin-top: 0;
		padding-left: 0;
		margin-bottom: 10px;
	}

	.work-subtitle {
		position: static;
		padding: 0;
		margin-bottom: 0px;
	}

	.work-position {
		position: static;
		padding: 0;
		margin-bottom: 0px;
	}

	.work-duration {
		position: static;
		width: 100%;
		padding: 0;
		text-align: left;
		color: var(--secondary-color);
		font-weight: 500;
		margin-top: 10px;
	}

	.space {
		height: 40px;
	}
}
