.projects-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.projects-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
	z-index: 999;
}

.projects-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px;
}

@media (max-width: 600px) {
	/* Hide logo on mobile */
	.projects-logo {
		display: none;
	}

	.projects-container {
		margin-top: -10px;
	}

	.projects-project {
		width: calc(100% / 2); /* Adjust project width on mobile */
	}
}

.page-content {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.content-wrapper {
	flex: 1;
	display: flex;
	flex-direction: column;
	margin-bottom: -75px; /* Match this with padding-bottom */
}

.projects-container {
	flex: 1;
	padding-bottom: 75px; /* Creates space for footer */
}

